.created-item-content {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}
.delete-btn {
    width: 2rem;
    height: 2.25rem;
    background-image: url(../../public/icon-delete-light.png);
    background-position: center;
    background-repeat: no-repeat;
}